import wx from 'weixin-js-sdk'
import * as http from '../utils/http'
import sha1 from 'sha1'

const wxApi = {
    /**
     * [wxRegister Api]
     * @param  {Function} callback [ready]
     */
    wxRegister(callback) {
        // 
        http.fetch('/dhr/wechat/jsapi/ticket', {
            reqUrl: window.location.href
        }).then((res) => {
            console.log(res)
           // PS: 
           let noncestr = 'Wm3WZYTPz0wzccnW',
           timestamp = new Date().getTime(),
           jsapi_ticket = res.Result.ticket,
           url = location.href.split('#')[0]

            let string1 = `jsapi_ticket=${jsapi_ticket}&noncestr=${noncestr}&timestamp=${timestamp}&url=${url}`
            
            let signature = sha1(string1)
           console.log(signature)
            wx.config({
                debug: false, // 
                appId: res.AppID, // 
                timestamp: timestamp, // 
                nonceStr: noncestr, // 
                signature: signature, // 1
                jsApiList: [
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage'
                ] // JSJS2
            })
        }).catch((error) => {
            console.log(error)
        })
        wx.ready((res) => {
            // ready
            if (callback) {
                callback()
            }
        })
    },
    /**
     * [ShareTimeline ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareTimeline(option) {
        wx.onMenuShareTimeline({
            title: option.title, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    },
    /**
     * [ShareAppMessage ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareAppMessage(option) {
        wx.onMenuShareAppMessage({
            title: option.title, // 
            desc: option.desc, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    }
}
export default wxApi