import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'
import axios from 'axios'
import {
  fetch,
  post
} from './utils/http'
import fastclick from 'fastclick'; //300ms
// px to rem 
import 'lib-flexible'

// vant 
import 'vant/lib/index.css';
import {
  Dialog,
  Uploader,
  DatetimePicker,
  Popup,
  Swipe,
  SwipeItem,
  Toast,
  Lazyload,
  PullRefresh,
  List,
  Loading 
} from 'vant';

Vue.use(Dialog)
  .use(Uploader)
  .use(DatetimePicker)
  .use(Popup)
  .use(Swipe)
  .use(SwipeItem)
  .use(Toast)
  .use(Lazyload, {
    loading: ''
  })
  .use(PullRefresh)
  .use(List)
  .use(Loading)

// 300ms
// fastclick.attach(document.body);

/*if*/
console.log(process.env.VUE_APP_FLAG)
if (process.env.NODE_ENV === 'production') {
  /*if.envVUE_APP_FLAG*/
  if (process.env.VUE_APP_FLAG === 'pro') {
    //production 
    axios.defaults.baseURL = 'http://m.news.qhiwi.com';

  } else {
    //test 
    axios.defaults.baseURL = 'http://120.78.158.34/';

  }
} else {
  //dev 
  // axios.defaults.baseURL = 'http://120.78.158.34/';
}


Vue.prototype.$http = axios
Vue.prototype.$fetch = fetch
Vue.prototype.$post = post

Vue.config.productionTip = false


Vue.filter('goTime', (value) => {
  return Math.floor(value / (24 * 3600 * 1000))
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')