<template>
  <div class="house_detail">
    <van-swipe @change="onChange">
      <!-- :style="`width:${viewWidth}px`" -->
      <van-swipe-item v-for="item in 4">
        <div class="hd_img">
          <img src="../../assets/images/house/hd.png">
        </div>
      </van-swipe-item>
      <div class="custom-indicator" slot="indicator">{{ current + 1 }}/4</div>
    </van-swipe>

    <div class="details">
      <div class="d_t">
        <h3>{{ house_detail.title }}</h3>
        <div class="addr">
          <p>{{ house_detail.belongCountryName }}  {{ house_detail.belongCityName }} {{ house_detail.labels }}</p>
        </div>

        <div class="cans">
          <div class="cans_i">
            <p class="pri">
              
              <span>{{ house_detail.price }}</span>
            </p>
            <p class="dottal"></p>
          </div>
          <div class="cans_i">
            <p class="pri">
              <span>{{ house_detail.downPay }}</span>%
            </p>
            <p class="dottal"></p>
          </div>
          <div class="cans_i">
            <p class="pri">
              <span>{{ house_detail.returnRate }}</span>%
            </p>
            <p class="dottal"></p>
          </div>
        </div>

        <div class="yh">
          <p class="yh_l"></p>
          <p class="yh_r">{{ house_detail.discount }}</p>
        </div>

        <div class="dl" style="display:none">
          <p class="dl_l">
            
            <span></span>
          </p>

          <p class="dl_r">
            
            <span></span>
          </p>
        </div>
      </div>

      <div class="d_b">
        <div class="dbl">
          <p class="dbld">
            
            <span>{{  house_detail.minArea}}~{{ house_detail.maxArea }}</span>
          </p>
          <p class="dbld">
            
            <span>{{ house_detail.hoseType }}</span>
          </p>
          <p class="dbld">
            
            <span>{{house_detail.deliverCriterion}}</span>
          </p>
          <p class="dbld">
            
            <span>{{ house_detail.optionalRoomType }}</span>
          </p>
        </div>
        <div class="dbl">
          <p class="dbld">
            
            <span>{{ house_detail.propertyRightYears }}</span>
          </p>
          <p class="dbld">
            
            <span></span>
          </p>
          <p class="dbld">
            
            <span>{{ house_detail.deliveryTime }}</span>
          </p>
        </div>
      </div>
    </div>

    <div class="project">
      <van-tabs v-model="active" title-active-color="#ED2530" :line-height="1">
        <van-tab title="">
          <div class="content" v-html="house_detail.content">
            <!-- <p></p>

            <div class="imgs">
              <img src="../../assets/images/house/pro.png">
            </div>

            <p></p>
            <p></p> -->
          </div>
        </van-tab>
        <van-tab title="">
          <div class="zb_content">
            <div class="education">
              <h3></h3>
              <p>Pimpama</p>
              <p>Pimpama-</p>
              <p></p>
            </div>
            <div class="shop">
              <h3></h3>
              <p>
                CoomeraUpper CoomeraPimpama Junction
                
              </p>
            </div>

            <div class="leisure">
              <h3></h3>
              <p>5.8</p>
              <p>-6.9</p>
              <p></p>
            </div>

            <div class="hospot">
              <h3></h3>
              <p>5Pimpama</p>
              <p>5MedicrossCoomera</p>
              <p>25</p>
            </div>

            <div class="traffic">
              <h3></h3>
              <p>5Ormeau</p>
              <p>7Coomera </p>
              <p>40CBD</p>
              <p>30CBD</p>
            </div>
          </div>
        </van-tab>
        <van-tab title="">
          <div class="wy_content">
            <div class="kt">
              <h3></h3>
              <p><span v-if="property.airConditioning.SplitType"></span> </p>
            </div>
            <div class="cf">
              <h3></h3>
              <p><span v-if="property.kitchen.Refrigerator"></span> </p>
              <p><span v-if="property.kitchen.Oven.Washingmachine"></span> </p>
              <p><span v-if="property.kitchen.Dishwasher"></span> </p>
              <p><span v-if="property.kitchen.MicrowaveOven"></span> </p>
              <p><span v-if="property.kitchen.Heater"></span> </p>
              <p><span v-if="property.kitchen.Cupboard"></span> </p>
              <p><span v-if="property.kitchen"></span> </p>
            </div>
            <div class="wy">
              <h3></h3>
              <p><span v-if="property.bathroom.Bathtub"></span> </p>
              <p><span  v-if="property.bathroom.shower"></span> </p>
              <p><span v-if="property.bathroom.Basin"></span> </p>
              <p><span v-if="property.bathroom.Pedestalpan"></span> </p>
            </div>

            <div class="tcc">
              <h3></h3>
              <p><span v-show="property.Garden.PrivateCourtyard"></span> </p>
              <p><span></span> </p>
            </div>

            <div class="hy">
              <h3></h3>
              <!-- <p> </p> -->
              <p><span v-show="property.Garden.PrivateCourtyard"></span> </p>
              <p><span v-if="property.Garden.SwimmingPool"></span> </p>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>

    <con :simpleName="''"/>
  </div>
</template>

<script>
import { Tab, Tabs } from "vant";
import con from "../../components/conf";
export default {
  props: {},
  data() {
    return {
      current: 0,
      active: 0,
      house_detail: {},
      property: {}
    };
  },
  created() {
    this.getHouseDetail()
  },
  methods: {
    getHouseDetail() {
      let { id } = this.$route.params
      this.$fetch(`/dhr/client/house/${id}`).then(res => {
        console.log(res.Result)
        if (res.ErrCode == '0000') {
          this.house_detail = res.Result
          this.property = JSON.parse(res.Result.property)
          console.log(this.property)
        }
      })
    },
    onChange(index) {
      this.current = index;
    }
  },
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    con
  }
};
</script>

<style lang="scss">
.house_detail {
  width: 100%;
  .custom-indicator {
    position: absolute;
    width: 90px;
    height: 36px;
    border-radius: 18px 0px 0px 18px;
    background-color: #ed2530;
    text-align: center;
    line-height: 36px;
    color: #fff;
    right: 0;
    bottom: 20px;
  }
  .van-tabs__content {
    padding: 0 30px !important;
  }

}
</style>


