            background: url("../../assets/images/house/cf.png") no-repeat
              center/cover;
          }
        }
      }

      .wy {
        h3 {
          &::after {
            background: url("../../assets/images/house/wy.png") no-repeat
              center/cover;
          }
        }
      }

      .tcc {
        h3 {
          &::after {
            background: url("../../assets/images/house/tcc.png") no-repeat
